


html, body {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  font-family: sans-serif;
  background-image: linear-gradient(to top, #29323c, #313a45, #38434f, #404c59, #485563);
}

#root {
  height: 100%
}

.App {
  text-align: center;
  height: 100%;
  position: absolute;
}

.carouselStyling {
  display: flex;
  align-items: center;
}

.horizontal-right-rows {
  margin-top: 10em;
  margin-bottom: 10em;
  padding-left: 5%
}

.flipIcon {
-webkit-transform: scaleX(-1);
transform: scaleX(-1);
-webkit-filter: FlipH;
        filter: FlipH;
-ms-filter: "FlipH";
}

/*grid styling for the horizontal orientation*/
.horizontal-styling {
  display: grid;
  height: 100%;
}

.horizontalAlbumImage {
  grid-column: 1 / span 3;
  grid-row: 1 / span 8;
  height: 100%;
}

.horizontalAlbumInfo {
  grid-column: 3 / span 2;
  height: 100%
}

.center-navbar {
  text-align: center;
  padding-top: 2%
}

.horizontal-text {
  margin-left: 5%;
  margin-right: 5%;
}

.center-text {
  text-align: center;
}

.setcounter-margins {
  margin-bottom: 0px
}

/* Below is new CSS added during migration to CSS-Grid */
.landscape-container {
  padding: 2em;
  height: 100%;
  display: grid;
  grid-gap: 1px;
  grid-column-gap: 1em;
  -webkit-column-gap: 1em;
          column-gap: 1em;
  grid-template-columns: 11fr 9fr;
  grid-template-rows: 1.5fr .5fr 3fr;
  grid-template-areas:
  "albumCover navbar"
  "albumCover albumCounter"
  "albumCover albumTitle"
  "albumCover albumText"
}

.portrait-container {
  display: grid;
  height: 100%;
  padding: .5rem;
  grid-gap: .5rem;
  grid-template-rows: 5fr 0.5fr 0.25fr 3fr;
  grid-template-areas:
  "albumCover"
  "navbar"
  "albumCounter"
  "albumTitle"
  "albumText"
}

.portrait-margins {
  margin: 0 3em;
}

.albumCover {
  grid-area: albumCover;
  height: 100%;
}

.navbar {
  grid-area: navbar;
}

.navbar > div {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border-bottom: 2px solid black
}

.albumCounter {
  grid-area: albumCounter;
}

.albumCounter > div {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.albumCounter > div {
  vertical-align: top;

}

.albumTitle {
  grid-area: albumTitle;
}

.albumTitle > div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem
}

.albumText {
  grid-area: albumText;
  text-align: center;
}

a {
  color: darkgrey
}

.overlay-styling {
  position: absolute;
  z-index: 10;
  color: #FFFFFF;
  font-family: 'Roboto', sans-serif;
  background-color: rgba(25, 24, 26, 0.9);
  height: 100%;
  width: 100%;
  transition: 0.5s;
  transition: height 500ms 0ms, opacity 500ms 500ms z-index 500ms 500ms;
  overflow: auto
}

.overlay-styling.hide {
  transition: height 500ms 500ms, opacity 500ms 0ms z-index 500ms 500ms;
  opacity: 0;
  height: 0px;
  z-index: 0;
  overflow: hidden;
}

.overlayText {
  padding-top: 0rem;
  padding-left: 1em;
  padding-right: 1em;
  text-align: justify;
}

h1 {
  text-align: center;
}

/* To go up to tablet size */
@media only screen and (min-width: 768px) {
  /* For desktop: */
  .overlayText {
  padding-left: 8rem;
  padding-right: 8rem;
  font-size: 1.2em;
  text-align: center
 }
}

/* for centering vertically and horizontally on larger screens */
@media only screen and (min-width: 1200px) {
  /* For desktop: */
  .overlayText {
  padding-left: 13rem;
  padding-right: 13rem;
  font-size: 1.2em;
  text-align: center
 }

 .flexcontainer {
   height: 100%;
   display: flex;
  justify-content: center;
